/* ***************** Services **************************** */
.skills {
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);
}

.skills h1 {
  color: #6bd425;
  color: var(--primary-green);
  text-transform: uppercase;
}

.skills .circle {
  position: relative;
  margin: 0.375rem auto;
  background: #6bd425;
  background: var(--primary-green);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.skills .icon {
  font-size: 1.4rem;
  color: #e2e2e2;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.skills .box {
  height: 16rem;
  border-bottom: 0.3125rem solid #6bd425;
  border-bottom: 0.3125rem solid var(--primary-green);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #e2e2e2;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
  text-align: center;
}

.skills .box:hover {
  background: #6bd425;
  background: var(--primary-green);
  border-bottom: 0.3125rem solid rgb(52, 58, 64);
  border-bottom: 0.3125rem solid var(--primary-dark);
}

.skills .box:hover .icon {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
}

.skills .box:hover .circle {
  background: #e2e2e2;
  background: var(--primary-white);
}

.skills .box:hover p {
  color: #e2e2e2;
  color: var(--primary-white);
}
:root {
    --primary-grey: #605f5e;
    --primary-dark-grey: #666;
    --primary-green: #6bd425;
    --primary-dark: rgb(52, 58, 64);
    --primary-blue: #247ba0;
  }
  
  /* ***************** Experience **************************** */
  .experience {
    padding: 0.7rem;
  }
  
  .experience-wrapper {
    position: relative;
    padding: 3.125rem 0;
    overflow: hidden;
  }
  
  .experience h1 {
    text-transform: uppercase;
    color: #6bd425;
    color: var(--primary-green);
  }
  
  .experience-wrapper::before {
    position: absolute;
    content: '';
    top: 0;
    left: 50%;
    margin-left: -0.0625rem;
    width: 0.125rem;
    height: 100%;
    background: #605f5e;
    background: var(--primary-grey);
    z-index: 1;
  }
  
  .timeline-block {
    width: calc(50% + 0.5rem);
    display: flex;
    justify-content: space-between;
    clear: both;
  }
  
  .timeline-block-right {
    float: right;
  }
  
  .timeline-block-left {
    float: left;
    direction: rtl;
  }
  
  .marker {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 0.3125rem solid rgb(52, 58, 64);
    border: 0.3125rem solid var(--primary-dark);
    background: #6bd425;
    background: var(--primary-green);
    margin-top: 0.625rem;
    z-index: 3;
  }
  
  .timeline-content {
    width: 95%;
    padding: 0 0.9375rem;
    color: #666;
    color: var(--primary-dark-grey);
    direction: ltr;
  }
  
  .timeline-content h3 {
    margin-top: 0.3125rem;
    color: #247ba0;
    color: var(--primary-blue);
    font-weight: 900;
  }
